.bar {
    max-width: 960px;
    padding: 10px 8px;
    margin: 2px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

.bar .left {
    display: flex;
    align-content: center;
}

.bar .right {
    display: flex;
    float: right;
}

.bar img {
    vertical-align: middle;
}

.link {
    font-size: 15px;
    padding: 10px;
    text-decoration: none;
    color: inherit;
    font-weight: bold;
}